.container {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  // outline: 1px solid green;

  @media screen and (min-width: 480px) and (max-width: 767px) {
    width: 450px;
    position: relative;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    width: 768px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}

.no-padding {
  padding-top: 0;
  padding-bottom: 0;
}